.stars {
    position: absolute;
    inset: 0;
    height: 100%;
}

.stars-bg {
    position: absolute;
    height: 100%;
    flex-direction: column;
    justify-content: flex-start;
    overflow: hidden;
}

.stars-bgRow {
}

.star-bgShape {
    height: 5rem;
    width: 5rem;
    /*border: 1px solid black;*/
    box-shadow: inset -3px -3px 3px rgba(0,0,0,0.25), inset 3px 3px 3px rgba(255,255,255,0.5);
}

.star-colors {
    position: absolute;
    inset: 0;
    height: 100%;
    background: linear-gradient(11deg, transparent, transparent, transparent, rgba(0, 6, 63, 0.5),  rgba(0, 6, 63, 0.5), rgba(224, 137, 118, 0.25), rgba(0,255,0,0.1),  rgba(0, 6, 63, 0.5), rgba(0, 6, 63, 0.5), transparent);
}

.star {
    position: absolute;
    height: 2px;
    width: 2px;
    background: white;
    border-radius: 50%;
    opacity: 0.1;
    transition: opacity 0.25s;
    animation: blink 3s infinite;
    z-index: 1;
}

.star::before {
    z-index: -1;
    content: '';
    position: absolute;
    height: 300%;
    width: 300%;
    border-radius: 50%;
    background: radial-gradient(circle, white, rgba(255,255,51,0.25), transparent);
}

@keyframes blink {
    0% {
        opacity: 0.1;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0.1;
    }
}
.star:hover {
    opacity: 1;
}

