.App {
  text-align: center;
  position: relative;
  opacity: 0;
  transition: opacity 0.25s 0.1s;
  overflow-x: hidden;
}

.text {
  font-size: 1.5rem;
  line-height: 1.9rem;
}
.header {
  height: 10vh;
}

.underline {
  height: 0.25rem;
  background: rgba(224, 137, 118, 0.5);
  margin: 1rem 0;
  max-width: 90%;
}

.space {
  position: relative;
  height: 10vh;
}

.space0 {
  position: absolute;
  bottom: -1px;
  background: transparent;
  /*z-index: 1;*/
}

.space-inner0 {
  clip-path: polygon(100% 100%, 0 0, 0 100%);
}

.space1 {
  background: #738A90;
}

.space2 {
  background: #738A90;
}

.space-inner {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  background: #333;
}

.space-inner1 {
  clip-path: polygon(100% 0, 1% 0, 100% 100%);
  background: #333;
  top: -1px;
}

.space-inner2 {
  clip-path: polygon(100% 100%, 0 0, 0 100%);
  background: #333;
  bottom: -1px;
}

.home {
  position: relative;
  width: 100%;
  padding-top: 5vh;
  padding-bottom: 5vh;
  position: relative;
  /*background:  radial-gradient(#021f4b, #dd7c86 ,#115268), #333;*/
  background-image: url("./images/background.svg");
  background-size: cover;
  flex-direction: column;
  justify-content: flex-start;
  text-align: left;
}

.home::before {
  content: '';
  position: absolute;
  inset: 0;
  opacity: 0.4;
  /*background:  radial-gradient(#422d53, #021f4b, #d1788f ,#115268, #021f4b), #333;*/
  background:  linear-gradient( to top left, #422d53, #d1788f, #115268, #422d53 ,#115268), #333;
}

.home > * {
  z-index: 100;
}

.home > h3 {
  text-align: center;
}

.home-textContainer {
  padding: 2rem 1rem;
  font-size: 1.25rem;
}

.home-text {
  margin: 0.5rem 0;
}

.avatarContainer-container {
  position: relative;
  height: 18rem;
  width: 18rem;
  margin: 3rem 0;
  padding: 11rem;
}

.avatarContainer-container > img {
  position: absolute;
  height: 36rem;
  width: 36rem;
  object-fit: contain;
  opacity: 0.75;
}

.avatar-bg {
  position: absolute;
  height: 22rem;
  width: 22rem;
  border: 1px inset rgba(255,255,255,0.75);
  background: rgba(255,255,255,0.25);
  backdrop-filter: blur(2px);
  border-radius: 50%;
  box-shadow: 5px 5px 5px 3px rgba(0,0,0,0.25);
}

.avatarContainer {
  position: relative;
  border: 1px inset rgba(255,255,255,0.5);
  background: rgba(255,255,255,0.25);
  backdrop-filter: blur(5px);
  border-radius: 50%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 18rem;
  width: 18rem;
  box-shadow: 5px 5px 5px 3px rgba(0,0,0,0.25);
}

.avatar {
  position: absolute;
  width: 15rem;
  height: 15rem;
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 5px 5px 5px 3px rgba(0,0,0,0.25);
}

.avatarInner {
  position: absolute;
  width: 80%;
  height: 80%;
  background-image: url('./images/avatar.svg');
  background-size: cover;
  border-radius: 50%;
  opacity: 0;
  transition: opacity 0.25s;
  box-shadow: 5px 5px 5px 3px rgba(0,0,0,0.5);
}

.opacity1 {
  opacity: 1;
}

.home-bgContainer {
  position: relative;
  width: 20rem;
  height: 20rem;
  transform: rotate(11deg) skew(11deg);
}

.home-bgContainer::after {
  content: '';
  position: absolute;
  inset: 0;
  backdrop-filter: blur(1px);
  z-index: 2;
}

.bgColor {
  position: relative;
  width: 20rem;
  height: 20%;
  z-index: 1;
}

.bgColorInner {
  position: absolute;
  left: -100%;
  transition: all 0.5s;
  height: 100%;
  width: 100%;
  z-index: -1;
}

.bgColorInner1 {
}

.bgColorInner2 {
  transition-delay: 0.1s;
}

.bgColorInner3 {
  transition-delay: 0.2s;
}

.bgColorInner4 {
  transition-delay: 0.3s;
}

.bgColorInner5 {
  transition-delay: 0.4s;
}

.color {
  left: 0;
}

.projects {
  padding-top: 5vh;
  padding-bottom: 10vh;
  flex-direction: column;
  background: #333;
  /*z-index: 1;*/
}

.projects > h1 {
  padding: 0 1rem;
}

.projects > * {
  /*z-index: 1000;*/
}

.projects-container {
  flex-wrap: wrap;
}

.project {
  position: relative;
  height: 50vh;
  color: white;
  background-size: cover;
  background-position: center;
  margin: 0.5rem;
  box-shadow: 3px 3px 3px 3px rgba(0,0,0,0.25);
}

.project-overlay {
  overflow: hidden;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 1rem;
  text-align: left;
  position: absolute;
  height: 100%;
  width: 100%;
  inset: 0;
  backdrop-filter: blur(3px);
  background: rgba(0,255,0,0.25);
  opacity: 0;
  transition: opacity 0.25s;
}

.project-description {
  transform: translateX(100%);
  transition: all 0.25s;
}

.project-titleUnderline {
  height: 2px;
  background: white;
  width: 1%;
  transition: all 0.25s;
}

.project:nth-child(1) {
  background-image: url('./images/neuro.png');
  background-size: cover;
}

.project:nth-child(2) {
  background-image: url('./images/spotify.png');
}

.project:nth-child(3) {
  background-image: url('./images/delrio.png');
}

.project:nth-child(4) {
  background-image: url('./images/react.png');
}

.project:nth-child(5) {
  background-image: url('./images/gwent.png');
}

.project:nth-child(6) {
  background-image: url('./images/business.png');
}

.project:nth-child(7) {
  background-image: url('./images/kelly.png');
}

.project:nth-child(8) {
  background-image: url('./images/hairdresser.png');
}

.project-links {
  transform: translateY(100%);
  transition: all 0.25s;
}
.project-links > * {
  margin: 0 1rem;
  cursor: pointer;
  opacity: 0.75;
  transition: opacity 0.25s;
}

.scrolled.opacity {
  opacity: 1 !important;
}

.scrolled.x {
  transform: translateX(0);
}

.scrolled.y {
  transform: translateY(0);
}

.scrolled.scale {
  transform: scaleX(89);
  transform-origin: left;
}

.about {
  background: #738A90;
  flex-direction: column;
  text-align: left;
  padding: 5rem 1rem;
}

.contact {
  flex-direction: column;
  position: relative;
  padding-top: 5rem;
  background: #333;
  overflow: hidden;
  justify-content: flex-start;
}

.contact-container {
  /*border: 2px solid white;*/
  box-shadow: -2px -2px 2px rgba(255,255,255,0.25), 2px 2px 2px rgba(0,0,0,0.75);
  /*background: #FFBA6A;*/
  height: 4rem;
  width: 4rem;
  margin: 1rem;
  cursor: pointer;
  opacity: 1;
  transition: all 0.25s;
  color: #333;
  border-radius: 0.25rem;
  z-index: 2;
}

.contact-container > * {
  transition: all 0.25s;
  color: white;
  transform: scale(1.5);
  opacity: 0.75;
  /*filter: drop-shadow(0 0 5px rgb(255, 186, 106)) drop-shadow(0 0 5px rgb(255, 186, 106));*/
}

.contact-container:nth-child(1) > * {
  filter: drop-shadow(0 0 5px #0072b1) drop-shadow(0 0 5px #0072b1);
}

.contact-container:nth-child(2) > * {
  filter: drop-shadow(0 0 5px #6cc644) drop-shadow(0 0 5px #6cc644);
}

.contact-container:nth-child(3) > * {
  filter: drop-shadow(0 0 5px #bd2c00) drop-shadow(0 0 5px #bd2c00);
}

.contact-container:hover > * {
  opacity: 1;
}

.upArrow {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  height: 3rem;
  width: 3rem;
  border-radius: 0.5rem;
  margin-top: 5rem;
  margin-bottom: 1rem;
  cursor: pointer;
  transition: opacity 0.25s;
z-index: 100;
  box-shadow: -2px -2px 2px rgba(255,255,255,0.25), 2px 2px 2px rgba(0,0,0,0.75);
}

.upArrow > * {
  opacity: 0.75;
  transition: opacity 0.25s;
  filter: drop-shadow(0 0 5px rgb(255, 186, 106)) drop-shadow(0 0 5px rgb(255, 186, 106));
}

.upArrow:hover > * {
  opacity: 1;
}
