#sky {
  position: relative;
  width: 18rem;
  height: 18rem;
  display: flex;
  border-radius: 50%;
  border: 1px solid white;
  justify-content: center;
  align-items: center;
  transition: all 1s;
}

.Sun, .Moon {
  border-radius: 50%;
}

.Sun {
  background: linear-gradient(45deg, yellow, orange);
  top: -1rem;
  position: absolute;
  box-shadow: 0 0 5px 5px rgba(255,255,0,0.5);
}

.Moon {
  background: linear-gradient(45deg, #fff, #343434);
  position: absolute;
  bottom: -1rem;
}

.skyInner {
  height: 2rem;
  width: 2rem;
  position: absolute;
}

.moon-crater {
  border: 1px solid rgba(0,0,0,0.25);
  border-radius: 50%;
  position: absolute;
  background: #343434;
  opacity: 0.25;
  box-shadow: inset -3px -3px 5px rgba(255,255,255,0.25), inset 3px 3px 5px rgba(0,0,0,0.75)
}

.moon-crater:nth-child(1) {
  height: 0.4rem;
  width: 0.5rem;
  top: 0.5rem;
  left: 1rem;
  transform: rotate(65deg);
}

.moon-crater:nth-child(2) {
  left: 0.1rem;
  bottom: 0.75rem;
  height: 0.5rem;
  width: 0.6rem;
  transform: rotate(50deg);
}

.moon-crater:nth-child(3) {
  height: 0.45rem;
  width: 0.6rem;
  top: 1.3rem;
  right: 0.4rem;
  transform: rotate(20deg);
}

.nadir { transform: rotate(180deg) !important ;}
.nightEnd { transform: rotate(190deg) !important ;}
.nauticalDawn { transform: rotate(100deg) !important ;}
.dawn { transform: rotate(100deg) !important ;}
.sunrise { transform: rotate(90deg) !important ;}
.sunriseEnd { transform:rotate(65deg) !important ;}
.goldenHourEnd { transform: rotate(45deg) !important ;}
.solarNoon { transform: rotate(0deg) !important ;}
.goldenHour { transform: rotate(-45deg) !important ;}
.sunsetStart { transform: rotate(-65deg) !important ;}
.sunset { transform: rotate(150deg) !important ;}
.dusk { transform: rotate(-100deg) !important ;}
.nauticalDusk { transform: rotate(-100deg) !important ;}
.night { transform: rotate(175deg) !important ;}
