.darkFilter {
  position: absolute;
  height: 100%;
  inset: 0;
  -webkit-backdrop-filter: brightness(110%) contrast(110%);
          backdrop-filter: brightness(110%) contrast(110%);
}

.parallax {
  -webkit-perspective: 1px;
          perspective: 1px;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
}

.parallax-group {
  position: relative;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  overflow: hidden;
}

.parallax-layer {
  /*height: 100%;
  width: 100%;*/
  position: absolute;
  inset: 0;
}

.parallax-base {
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
}

.parallax-back {
  -webkit-transform: translateZ(-1px) scale(2);
          transform: translateZ(-1px) scale(2);
}

