}

.Sun {
  background: linear-gradient(45deg, yellow, orange);
  top: -1rem;
  position: absolute;
  box-shadow: 0 0 5px 5px rgba(255,255,0,0.5);
}

.Moon {
  background: linear-gradient(45deg, #fff, #343434);
  position: absolute;
  bottom: -1rem;
